import GirafeHTMLElement from '../../base/GirafeHTMLElement';
import Theme from '../../models/theme';


class ThemeFavoriteComponent extends GirafeHTMLElement {

  templateUrl = './template.html';
  styleUrl = './style.css';

  #themesList?: HTMLUListElement;
  #ignoreBlur = false;

  favthemes: {
    [key: number]: Theme
  } = {};

  constructor() {
    super('themelocationiconswitcher');
  }

  get themesList() {
    if (!this.#themesList) {
      throw new Error('You called themesList before render');
    }
    return this.#themesList;
  }

  render(x: boolean = false) {
    super.render();
    this.#themesList = this.shadow.querySelector('#themes')!;
    this.toggleThemesList(false);

    if (x) {
      this.toggleThemesList(true);
    }
  }

  registerEvents() {
    this.stateManager.subscribe('loading', (x: boolean) => this.render(x));
    this.stateManager.subscribe('themes', () => { this.filterThemes(); this.render(); super.girafeTranslate(); });
  }


  onBlur() {
    if (!this.#ignoreBlur) {
      this.toggleThemesList(false);
    }
  }

  filterThemes() {
    for (const theme of Object.values(this.state.themes)) {
      if (theme.favorite == true) {
        this.favthemes[theme.id] = theme;
      }
    }
  }


  toggleThemesList(forceDisplay: null | boolean = null) {
    if (forceDisplay === true) {
      this.themesList.style.display = 'block';
    }
    else if (forceDisplay === false) {
      this.themesList.style.display = 'none';
    }

    else if (this.themesList.style.display === 'none') {
      this.themesList.style.display = 'block';
    }
    else {
      this.themesList.style.display = 'none';
    }
  }

  ignoreBlur() {
    this.#ignoreBlur = true;
  }


  onThemeChanged(theme: Theme) {
    this.state.selectedTheme = theme;

    if (theme.location != null || theme.zoom != null) {
      this.state.olMap?.getView().animate({
        center: theme.location ?? this.state.olMap?.getView().getCenter(),
        zoom: theme.zoom ?? this.state.olMap?.getView().getZoom(),
        duration: 1000
      })
    }

    this.toggleThemesList(true);
    this.#ignoreBlur = false;
  }

  connectedCallback() {
    this.loadConfig()
      .then(() => {
        this.render();
        this.registerEvents();
      });
  }
}
export default ThemeFavoriteComponent;

