import { Coordinate } from "ol/coordinate";
import BaseLayer from "./layers/baselayer";

class Theme {
  // Properties
  id: number;
  icon: string;
  name: string;
  location?: Coordinate;
  zoom?: number;
  favorite?: boolean;


  // Layers
  layersTree: BaseLayer[] = [];


  constructor(elem: { id: number, name: string, icon: string, location?: Coordinate, zoom?: number, favorite?: boolean }) {

    this.id = elem.id;
    this.name = elem.name;
    this.icon = elem.icon;
    this.location = elem.location;
    this.zoom = elem.zoom;
    this.favorite = elem.favorite;
  }
}

export default Theme;
